import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  useTheme,
  IconButton,
  Tooltip,
} from "@mui/material";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import { cartActions, productActions } from "../../redux";

export default function ProductList({ onProductClick }) {
  const theme = useTheme();
  const dispatch = useDispatch();
  const { productList, status } = useSelector((state) => state.products);

  useEffect(() => {
    if (status === "idle") {
      dispatch(productActions.fetchProductsByCategory());
    }
  }, [status, dispatch]);

  const handleAddToCart = (product) => {
    dispatch(cartActions.addToCart({ ...product, quantity: 1 }))
  };

  return (
    <TableContainer component={Paper} sx={{ backgroundColor: theme.palette.background.paper }}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Description</TableCell>
            <TableCell>Price</TableCell>
            <TableCell align="center">Cart</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {productList.map((p, index) => (
            <TableRow
              key={p.id || index}
              onClick={() => onProductClick(p)}
              sx={{
                cursor: "pointer",
                "&:hover": { backgroundColor: theme.palette.action.hover },
              }}
            >
              <TableCell>{p.name}</TableCell>
              <TableCell>{p.description}</TableCell>
              <TableCell>{p.price}</TableCell>
              <TableCell align="center">
                <Tooltip title="Add to Cart">
                  <IconButton
                    color="primary"
                    size="small"
                    onClick={(e) => {
                      e.stopPropagation(); 
                      handleAddToCart(p);
                    }}
                  >
                    <AddShoppingCartIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
