import { createContext, useState, useEffect } from "react";
import axios from "axios";

//  Context
export const ProductContext = createContext();

export function ProductProvider({ children }) {
  const [productList, setProductList] = useState([]);  // 
  const [selectedProduct, setSelectedProduct] = useState(null); // 
  const [selectCategory, setCategory] = useState({"id": 0, "name":"All Categories"}); // 

    
  // 
  const handleProductClick = (product) => {
    setSelectedProduct(product);
  };

  // 
  const handleBack = () => {
    setSelectedProduct(null);
  };

  // 
  const handleCategoryChange = (newSelection) => {
    
    setCategory(newSelection);
  };

  // 
  const addProduct = (product) => {
    setProductList((prevList) => [...prevList, product]);
  };

  // 
  const removeProduct = (productId) => {
    setProductList((prevList) => prevList.filter(item => item.id !== productId));
  };

  // 
  useEffect(() => {
    const fetchAllItems = async () => {
      try {
        const response = await axios.get("http://localhost:8222/api/v1/products");
        setProductList(response.data);
      } catch (error) {
        console.error("Error fetching all items:", error);
      }
    };
    fetchAllItems();
  }, []);


  // 
  useEffect(() => {
    const url = selectCategory
      ? `http://localhost:8222/api/v1/products/category?selectedCategory=${selectCategory.id}`
      : "http://localhost:8222/api/v1/products";

    const fetchData = async () => {
      try {
        const response = await axios.get(url);
        setProductList(response.data);
      } catch (error) {
        console.error("Error fetching product:", error);
      }
    };

    fetchData();
  }, [selectCategory]);

  return (
    <ProductContext.Provider value={{
      productList, selectedProduct, selectCategory,
      handleProductClick, handleBack, handleCategoryChange,
      addProduct, removeProduct, setProductList
    }}>
      {children}
    </ProductContext.Provider>
  );
}
