import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../contexts";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Box,
  Switch,
  Badge,
  Drawer,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import NavbarMenu from "./NavbarMenu";
import NavbarDrawer from "./NavbarDrawer"; //  
import { useSelector, useDispatch } from "react-redux";
import { themeActions } from "../../redux";

export default function UnderConstruction() {
  const { auth } = useContext(UserContext);
  const [mobileOpen, setMobileOpen] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { mode } = useSelector((state) => state.theme);
  const cartItems = useSelector((state) => state.cart.itemsById || {});
  const totalItems = Object.values(cartItems).reduce(
    (sum, item) => sum + item.quantity,
    0
  );

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleCartClick = () => {
    navigate("/cart");
  };

  return (
    <>
      {/*  */}
      <Box
        sx={{
          width: "100%",
          bgcolor: "#FFD700",
          color: "black",
          textAlign: "center",
          py: 1,
          fontWeight: "bold",
          fontSize: 16,
          letterSpacing: 1,
        }}
      >
         Under construction  but at least we dont need hard hats! 
      </Box>

      {/*  */}
      <AppBar position="sticky" color="primary">
        <Toolbar>
          <Typography variant="h6" sx={{ flexGrow: 1 }}>
            My Store
          </Typography>

          {/*  */}
          <Switch
            checked={mode === "dark"}
            onChange={() => dispatch(themeActions.toggleMode())}
            color="default"
          />

          {/*  */}
          <IconButton color="inherit" sx={{ mr: 2 }} onClick={handleCartClick}>
            <Badge
              badgeContent={totalItems}
              color="error"
              invisible={totalItems === 0}
            >
              <ShoppingCartIcon />
            </Badge>
          </IconButton>

          {/*  */}
          <NavbarMenu auth={auth} />

          {/*  */}
          <IconButton
            color="inherit"
            edge="end"
            onClick={handleDrawerToggle}
            sx={{ display: { xs: "block", md: "none" } }}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/*   Drawer */}
      <Drawer
        anchor="right"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        transitionDuration={300} //  
      >
        <NavbarDrawer auth={auth} onClose={handleDrawerToggle} />
      </Drawer>
    </>
  );
}
