import { jwtDecode } from 'jwt-decode';

export const isTokenExpired = (token) => {
    if (!token) return true;

    try {
        const { exp } = jwtDecode(token); //  token 
        if (!exp) return true; // 

        const currentTime = Date.now() / 1000; // 
        return exp < currentTime; //  token 
    } catch (error) {
        return true; //  token 
    }
};

