import React from "react";
import { Box } from "@mui/material";
import { useTheme } from "@mui/material/styles";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";

export default function AboutMe() {
  const theme = useTheme();
  const isDarkMode = theme.palette.mode === "dark";

  const sentence =
    "I am a developer focused on building scalable applications. I constantly explore new tech to enhance my problem-solving skills, and this project showcases my ability to combine design and...";

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, ease: "easeOut" }}
      viewport={{ once: true, amount: 0.3 }}
    >
      {/*  */}
      <div className="icon mb-3">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <g fill="none" fillRule="evenodd">
            <path d="M0 0h24v24H0z"></path>
            <path
              d="M7 3h10a4 4 0 110 8H7a4 4 0 110-8zm0 6a2 2 0 100-4 2 2 0 000 4z"
              fill="#335EEA"
            ></path>
            <path
              d="M7 13h10a4 4 0 110 8H7a4 4 0 110-8zm10 6a2 2 0 100-4 2 2 0 000 4z"
              fill="#335EEA"
              opacity=".3"
            ></path>
          </g>
        </svg>
      </div>

      {/*  */}
      <Box
        component="h5"
        sx={{
          position: "relative",
          display: "inline-block",
          fontSize: "1.25rem",
          fontWeight: 600,
          color: isDarkMode ? "#FFFFFF" : "#000000",
          "&::after": {
            content: '""',
            position: "absolute",
            left: 0,
            bottom: -4,
            width: "0%",
            height: "2px",
            backgroundColor: "#1976d2",
            transition: "width 0.3s ease",
          },
          "&:hover::after": {
            width: "100%",
          },
        }}
      >
        <Link to="/about" style={{ textDecoration: "none" }}>
          Who I Am
        </Link>
      </Box>

      {/*  + glow */}
      <p style={{ marginTop: "1rem" }}>
        {sentence.split(" ").map((word, i) => (
          <motion.span
            key={i}
            whileHover={{
              y: -3,
              rotate: -2,
              scale: 1.05,
              color: "#e91e63",
              textShadow: "0 0 6px #e91e63",
            }}
            transition={{ duration: 0.2 }}
            style={{
              display: "inline-block",
              marginRight: "6px",
              color: isDarkMode ? "#fff" : "#555",
              cursor: "default",
            }}
          >
            {word}
          </motion.span>
        ))}
      </p>
    </motion.div>
  );
}
