import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { ProductContext, UserContext } from "../../contexts";
import { useApi } from "../../services";

export default function PlaceOrderButton({ paymentMethod, data }) {
  const { productList, setProductList } = useContext(ProductContext);
  const { auth } = useContext(UserContext);
  const navigate = useNavigate();
  const api = useApi();

  const handlePlaceOrder = async () => {
    if (!paymentMethod) {
      alert("Please select a payment method");
      return;
    }

    if (!auth) {
      setProductList([]);
      navigate("/login");
      return;
    }

    // ID
    const groupedProducts = productList.reduce((acc, product) => {
      if (acc[product.Id]) {
        acc[product.Id].quantity += 1;
      } else {
        acc[product.Id] = { productId: product.Id, quantity: 1 };
      }
      return acc;
    }, {});

    const productsArray = Object.values(groupedProducts);

    const order = {
      reference: data.referenceNo,
      amount: data.total,
      paymentMethod,
      customerId: auth.userId,
      products: productsArray,
    };

    try {
      await api.post("/orders", order);
      alert("Order placed successfully!");
      setProductList([]);
    } catch (error) {
      console.error("Order placement failed:", error);
      alert("Failed to place order. Please try again.");
    }
  };

  return (
    <button
      type="button"
      className="btn btn-outline-success mt-5 btn-sm"
      onClick={handlePlaceOrder}
    >
      Place Order
    </button>
  );
}
