import React, { createContext, useState, useEffect } from 'react';
import {getUserDataFromToken} from "../features";

export const UserContext = createContext();

export const UserProvider = ({ children }) => {
    const [auth, setAuth] = useState(null);

    // token
    
    useEffect(() => {

        const storedToken = localStorage.getItem('token');
        if (storedToken) {
            const userData = getUserDataFromToken(storedToken);
            if (userData) {
                setAuth(userData);
            }
        }
    }, []);


    // 
    const login = (token, refreshToken) => {
        const userData = getUserDataFromToken(token);
        localStorage.setItem('token', token); // Access Token
        localStorage.setItem('refreshToken', refreshToken); // Refresh Token
        setAuth(userData);
    };

    // 
    const logout = () => {
       
        localStorage.removeItem('token');
        localStorage.removeItem('refreshToken'); // Refresh Token
        setAuth(null);
    };
 
    
    return (
        <UserContext.Provider value={{ auth, login, logout }}>
            {children}
        </UserContext.Provider>
    );
};
