import { useContext } from "react";
import { ProductContext  } from "../../contexts";
import {ProductDetails, ProductList} from "..";

export default function ProductListContainer() {
  const { selectedProduct, productList, handleProductClick, handleBack } = useContext(ProductContext );

  return (
    <div>
      {selectedProduct ? (
        <ProductDetails product={selectedProduct} onBack={handleBack} />
      ) : (
        <ProductList items={productList} onProductClick={handleProductClick} />
      )}
    </div>
  );
}
