import React, { useState } from "react";
import {PaymentMethodSelector, OrderSummary, PlaceOrderButton} from "../orders";
import ViewOrder from "./ViewOrder";

export default function PlaceOrder({ productData }) {
  const [paymentMethod, setPaymentMethod] = useState("");
  const [data, setData] = useState({ referenceNo: "", tax: 0, total: 0 });

  return (
    <div>
      <form>
        <PaymentMethodSelector paymentMethod={paymentMethod} setPaymentMethod={setPaymentMethod} />
        <OrderSummary productData={productData} setData={setData} />
        <PlaceOrderButton paymentMethod={paymentMethod} data={data} />
        <ViewOrder />
      </form>
    </div>
  );
}
