import "./App.css";
import { MainLayout } from "./components";
import { Signup } from "./pages";
import { Home, Login, Product, CartPage,AboutMePage, OverviewPage,TechPage } from "./pages";
import { UserProvider, ProductProvider } from "./contexts";
import { createTheme, ThemeProvider, CssBaseline } from "@mui/material"; 
import { useSelector } from "react-redux";
import { HashRouter as Router, Route, Routes } from "react-router-dom";
import React from "react";


function App() {

  const { mode, primaryColor } = useSelector((state) => state.theme);


  const theme = createTheme({
    palette: {
      mode, // light  dark 
      primary: {
        main: primaryColor, // Redux 
      },
    },
  });

  return (
    <ThemeProvider theme={theme}> 
      <CssBaseline />
      <UserProvider>
        <ProductProvider>
          <Router>
            <Routes>
              <Route path="/" element={<MainLayout />}>
                <Route index element={<Home />} />
                <Route path="product" element={<Product />} />
                <Route path="login" element={<Login />} />
                <Route path="signup" element={<Signup />} />
                <Route path="cart" element={<CartPage />} /> 
                <Route path="/about" element={<AboutMePage />} />
                <Route path="/overview" element={<OverviewPage />} />
                <Route path="/techStack" element={<TechPage />} />
              </Route>
            </Routes>
          </Router>
        </ProductProvider>
      </UserProvider>
    </ThemeProvider>
  );
}

export default App;
